import Image from "next/image";

const HomePage = () => {
  const deals = [
    {
      title: "Smart Watches",
      image: "/images/smartwatches.jpg",
      discount: "25%",
    },
    {
      title: "Laptops",
      image: "/images/laptops.jpg",
      discount: "15%",
    },
    {
      title: "GoPro Cameras",
      image: "/images/cameras.jpg",
      discount: "40%",
    },
    {
      title: "Headphones",
      image: "/images/headphones.jpg",
      discount: "25%",
    },
    {
      title: "Canon Cameras",
      image: "/images/canon.jpg",
      discount: "25%",
    },
  ];

  const categories = [
    {
      title: "Home and Outdoor",
      items: [
        { title: "Soft Chairs", price: "From USD 19" },
        { title: "Sofa & Chair", price: "From USD 19" },
        { title: "Kitchen Dishes", price: "From USD 19" },
        { title: "Smart Watches", price: "From USD 19" },
      ],
    },
    {
      title: "Consumer Electronics and Gadgets",
      items: [
        { title: "Smart Watches", price: "From USD 19" },
        { title: "Cameras", price: "From USD 89" },
        { title: "Headphones", price: "From USD 10" },
        { title: "Electric Kettle", price: "From USD 90" },
      ],
    },
  ];

  return (
    <div className="p-6 bg-gray-100 space-y-8">
      {/* Deals Section */}
      <div className="bg-white shadow rounded p-4">
        <h2 className="text-lg font-bold mb-4">Deals and Offers</h2>
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
          {deals.map((deal, index) => (
            <div
              key={index}
              className="flex flex-col items-center text-center border p-4 rounded shadow-sm"
            >
              <Image
                src={deal.image}
                alt={deal.title}
                width={150}
                height={150}
                className="object-cover rounded"
              />
              <h3 className="mt-2 text-sm font-bold">{deal.title}</h3>
              <p className="text-red-500 mt-1">{`-${deal.discount}`}</p>
            </div>
          ))}
        </div>
      </div>

      {/* Categories Section */}
      {categories.map((category, index) => (
        <div key={index} className="bg-white shadow rounded p-4">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg font-bold">{category.title}</h2>
            <button className="bg-blue-500 text-white px-4 py-2 rounded">
              Source Now
            </button>
          </div>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {category.items.map((item, itemIndex) => (
              <div
                key={itemIndex}
                className="flex flex-col items-center text-center border p-4 rounded shadow-sm"
              >
                <Image
                  src="/images/placeholder.jpg" // Replace with actual image paths
                  alt={item.title}
                  width={100}
                  height={100}
                  className="object-cover rounded"
                />
                <h3 className="mt-2 text-sm font-bold">{item.title}</h3>
                <p className="text-gray-600">{item.price}</p>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default HomePage;
